/* .App {
  text-align: center;
} */
.firstSubDIv{
  width: 50%;
  margin: auto;
  height: 400px;
  display: flex;
  padding-top: 50px;
  flex-direction: column;
  justify-content: space-around;
  text-align: left;
  align-items: flex-start;
}
.firstDiv{
  background-image: url('./assets/firstImg.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  height: 500px;
  width: 100%;
}
.firstDivTitle{
  margin: 0px;
  font-family: IBMPlexSans;
  font-size: 50px;
  /* font-weight:300; */
  color: #fafafa;
}
.firstSubDIv div h1:nth-child(1){
  font-size: 55px;
}
.firstDivA{
  text-decoration: none;
  color: #fff;
  display: flex;
  cursor: pointer;
  flex-direction: row;
  font-size: 30px;
  font-family: IBMPlexSans;
  justify-content: center;
  align-items: center;
}
/* .firstDivA:hover{
  text-decoration: underline;
} */
.secondDiv{
  background-image: url('./assets/secondImg.jpg');
  background-blend-mode:multiply;
  background-repeat: no-repeat;
  background-color: #383838;
  background-size: 100% 100%;
  background-position: center;
  padding:20px 0px;
  width: 100%;
}
.secondSubDiv{
  width: 50%;
  margin: auto; 
}
.seconDivP{
  color: #fff;
  font-family: IBMPlexSans;
  font-size: 20px;
  margin-top: 0px;
}
.skillsDiv{
  font-size: 20px;
  width: 100%;
  display: flex;
  flex-direction: row;
  color: #fff;
  font-family: IBMPlexSans;
  justify-content: space-between;
}
.skillsDiv span{
  display: flex;
  align-items: center;
  user-select: none;
}
.serviceParent{
display: flex;
flex-direction: row;
margin-top: 20px;
justify-content: space-between;
}
.serviceBox{
  width: 220px;
  height: 90px;
  border-radius: 10px;
  background-color: #000;
  display: flex;
  box-shadow: 0px 0px 15px 0px #afafaf;
  padding: 40px;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: IBMPlexSans;
  box-shadow: 0px 0px 15px 0px #616161;
  user-select: none;
  color: #fff;
}
.serviceBox:hover{
  box-shadow: 0px 0px 15px 0px #fff;
}
.LanguageParent{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.LanguageParent1{
  margin-top: 20px;
}
.webProjectsParent{
  width: 100%;
  margin-top: 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.webProjectsParent1{
  margin-top: 40px;
}

@media only screen and (max-width:1900px){
.firstSubDIv,.secondSubDiv{
  width: 60%;
}
}

@media only screen and (max-width:1600px){
  .firstSubDIv,.secondSubDiv{
    width: 75%;
  }
  }

  
@media only screen and (max-width:1300px){
  .firstSubDIv,.secondSubDiv{
    width: 85%;
  }
  }

@media only screen and (max-width:1100px){
  .serviceBox{
    width: 170px;
    height: 70px;
}  
}  

@media only screen and (max-width:900px){
  .firstSubDIv,.secondSubDiv{
    width: 95%;
  }
}

@media only screen and (max-width:830px){
  .serviceBox{
    padding: 20px;
    width: 185px;
    height: 90px;
}  
}  

@media only screen and (max-width:740px){
  .serviceParent{
    flex-direction: column;
    align-items: center;
    margin-top: 0px;
    /* padding: 20px; */
    /* width: 170px; */
    /* height: 90px; */
}  
.skillsDiv span{
  width: 50%;
  margin-top: 10px;
}
.skillsDiv{
  margin-top: 0px;
  flex-wrap: wrap;
}
.serviceBox{
  margin-top: 30px;
    padding: 30px;
    width: 220px;
    height: 100px;
  }
}  



@media only screen and (max-width:740px){
  .firstSubDIv,.secondSubDiv{
    width: 95%;
  }
  .webProjectsParent{
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 0px;
  }
  .firstDivTitle{
    font-size: 45px;
  }
  }

  @media only screen and (max-width:600px){
    .firstDivTitle{
      font-size: 40px;
    }
  }

  @media only screen and (max-width:500px){
    .skillsDiv span{
    width: 100%;
    margin-top: 10px;
  }
  .firstSubDIv div h1:nth-child(1){
    font-size: 50px;
  }
}

@media only screen and (max-width:450px){
  .firstDivTitle{
    font-size: 35px;
  }
  .LanguageParent{
    flex-wrap: wrap;
  }
  .skillsDiv,.seconDivP{
    font-size: 18px;
  }
  .firstDivA{
    font-size: 24px;
  }
}

@media only screen and (max-width:400px){
  .firstDivTitle{
    font-size: 32px;
  }
  .firstSubDIv div h1:nth-child(1){
    font-size: 42px;
  }

}